.button-menu {
  position: relative;
  display: flex;
  flex-direction: column;

  @media (min-width: 768px) {
    display: none;
  }

  &[open] {
    .close-menu-icon {
      display: block;
    }

    .menu-icon {
      display: none;
    }
  }

  &__summary {
    width: 30px;
    height: 38px;
    list-style: none;

    .close-menu-icon {
      display: none;
    }
  }

  &__body {
    width: 130px;
    display: flex;
    flex-direction: column;
    gap: 6px;
    background-color: var(--color-black);
    padding: 12px 14px;
    position: absolute;
    right: 0;
    border-radius: 0 0 4px 4px;
  }
}
