.banner {
  height: 451px;
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 44px;
  overflow: hidden;
  z-index: 1;
  @media (min-width: 768px) {
    height: 500px;
    flex-direction: row-reverse;
    padding-bottom: 0;
  }

  &::after {
    content: '';
    width: 100%;
    height: 100%;
    background: var(--color-vertical-gradient);
    position: absolute;
    bottom: 0;
    z-index: -1;
    @media (min-width: 768px) {
      background: var(--color-horizontal-gradient);
    }
  }

  &__earth-img {
    position: relative;
    margin-top: -105px;
    width: 300px;
    @media (min-width: 768px) {
      margin-top: 0;
      margin-right: -90px;
    }
    @media (min-width: 1248px) {
      width: 327px;
      margin-right: 62px;
    }

    img {
      width: 100%;
      height: 100%;
    }
  }

  &__bg {
    width: 100%;
    height: 100%;
    position: absolute;
    bottom: 0;
    z-index: -1;

    img {
      width: 100%;
    }
  }

  &__info {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 17px;
    padding: 0 34px;
    cursor: default;
    @media (min-width: 768px) {
      width: auto;
      align-items: flex-start;
      gap: 22px;
      padding: 0 0 0 30px;
    }
    @media (min-width: 1248px) {
      padding: 0 0 0 82px;
    }
  }

  &__info-title {
    max-width: 292px;
    font: var(--font-head-36);
    color: var(--color-white);
    text-align: center;
    @media (min-width: 768px) {
      max-width: 411px;
      font: var(--font-head-48);
      text-align: start;
    }
    @media (min-width: 1248px) {
      max-width: 693px;
      font: var(--font-head-72);
    }

    span {
      color: var(--color-pink);
    }
  }

  &__info-subtitle {
    font: var(--font-body-16);
    color: var(--color-white);
    @media (min-width: 768px) {
      font: var(--font-body-22);
    }
    @media (min-width: 1248px) {
      font: var(--font-body-32);
    }

    span {
      color: var(--color-yellow);
    }
  }
}
