:root {
  /** COLORS **/
  --color-yellow: #ebff00;
  --color-pink: #ff70d9;
  --color-ocean: #304571;
  --color-white: #fff;
  --color-black: #000;

  --color-main-gradient: linear-gradient(196.2deg, #0D1E48 0%, #304571 100%);
  --color-vertical-gradient: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(15, 20, 32, 0.79) 100%);
  --color-horizontal-gradient: linear-gradient(270deg, rgba(0, 0, 0, 0) 0%, rgba(15, 20, 32, 0.79) 100%);
  ;
  --color-link-gradient: linear-gradient(98.25deg, #EBFF00 18.56%, #FF70D9 76.02%);

  /** FONTS **/
  --font-head-72: 4.5rem/1 'Lato ExtraBold';
  --font-head-74: 4.625rem/1 'Lato ExtraBold';
  --font-head-48: 3rem/1 'Lato ExtraBold';
  --font-head-46: 2.875rem/1 'Lato ExtraBold';
  --font-head-40: 2.5rem/1 'Lato ExtraBold';
  --font-head-36: 2.25rem/1 'Lato ExtraBold';
  --font-head-32: 2rem/1.2 'Lato ExtraBold';
  --font-head-24: 1.5rem/1.2 'Lato ExtraBold';
  --font-head-18: 1.125rem/1.2 'Lato ExtraBold';

  --font-body-32: 2rem/1.2 'Lato ExtraBold';
  --font-body-26: 1.625rem/1.2 'Lato ExtraBold';
  --font-body-24: 1.5rem/1.2 'Lato ExtraBold';
  --font-body-22: 1.375rem/1.2 'Lato ExtraBold';
  --font-body-20: 1.25rem/1.2 'Lato Bold';
  --font-body-18: 1.125rem/1.555 'Lato ExtraBold';
  --font-body-16: 1rem/1.2 'Lato ExtraBold';
  --font-body-14: 0.875rem/1.2 'Lato ExtraBold';

  --font-button-18: 1.125rem/1.2 'Lato Bold';
  --font-button-22: 1.375rem/1.2 'Lato Bold';
  --font-button-20: 1.25rem/1.2 'Lato ExtraBold';
  --font-button-16: 1rem/1.2 'Lato Bold';

}
