@import './styles/normalize.css';
@import './styles/fonts.css';
@import './styles/variables.styles.css';

* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html, body {
  width: 100%;
  height: 100%;
}

html {
  font-size: 16px;
}

body {
  background: var(--color-main-gradient);
}

li {
  list-style-type: none;
}

a {
  text-decoration: none;
}
