.offers-section {
  padding: 50px 20px;
  @media (min-width: 768px) {
    padding: 55px 30px;
  }
  @media (min-width: 1248px) {
    padding: 60px 80px;
  }

  &__title {
    font: var(--font-head-18);
    color: var(--color-white);
    margin-bottom: 30px;
    cursor: default;
    @media (min-width: 768px) {
      font: var(--font-head-24);
    }
    @media (min-width: 1248px) {
      font: var(--font-head-32);
      margin-bottom: 40px;
    }
  }

  &__content {
    width: 100%;
    display: inline-flex;
    flex-direction: column;
    gap: 30px;
    @media (min-width: 768px) {
      flex-direction: row;
      flex-wrap: wrap;
    }
    @media (min-width: 1248px) {
      gap: 40px
    }


    & > * {
      @media (min-width: 768px) {
        flex-basis: calc(50% - 15px);
      }
      @media (min-width: 1248px) {
        flex-basis: calc(50% - 20px);
      }

      &:last-of-type,
      &:first-of-type {
        @media (min-width: 1248px) {
          flex-basis: 100%;
          flex-grow: 1;
        }
      }
    }

  }
}
