.offer-item {
  width: 100%;
  height: 350px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  gap: 22px;
  position: relative;
  border-radius: 10px;
  padding: 26px;
  z-index: 1;
  overflow: hidden;
  cursor: default;

  &__bg {
    width: 103%;
    height: 102%;
    position: absolute;
    top: 0;
    left: -2%;
    z-index: -1;
    @media (min-width: 1248px) {
      width: 105%;
      height: auto;
      top: -50%;
    }

    img {
      width: 100%;
      height: 100%;
    }
  }

  &__title {
    max-width: 280px;
    font: var(--font-head-40);
    color: var(--color-white);
    @media (min-width: 1248px) {
      font: var(--font-head-46);
    }
  }

  &__subtitle {
    font: var(--font-body-16);
    color: var(--color-white);
    @media (min-width: 1248px) {
      font: var(--font-body-24);
    }

    span {
      display: none;
      @media (min-width: 1248px) {
        display: inline-block;
      }
    }
  }

  &:after {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    background: var(--color-vertical-gradient);
    @media (min-width: 768px) {
      background: var(--color-horizontal-gradient);
    }
  }
}

.offer-item_long-top {
  .offer-item__bg {
    @media (min-width: 1248px) {
      top: -120%;
    }
  }

  .offer-item__title {
    @media (min-width: 1248px) {
      max-width: 780px;
      font: var(--font-head-74);
    }
  }
}

.offer-item_long-bottom {
  .offer-item__bg {
    @media (min-width: 1248px) {
      top: -100%;
    }
  }

  .offer-item__title {
    @media (min-width: 1248px) {
      max-width: 780px;
      font: var(--font-head-74);
    }
  }
}
