.secondary-btn {
  width: fit-content;
  padding: 7px 40px;
  gap: 10px;
  background: none;
  border-radius: 100px;
  border: 2px solid var(--color-yellow);
  font: var(--font-button-16);
  color: var(--color-yellow);
  cursor: pointer;
  @media (min-width: 768px) {
    padding: 14px 40px;
  }

  &:hover {
    opacity: 0.6;
  }
}
