.info-block {
  display: flex;
  flex-direction: column;
  gap: 17px;
  padding: 0 20px;
  position: relative;
  @media (min-width: 768px) {
    padding: 0 30px;
  }
  @media (min-width: 1248px) {
    padding: 0 80px;
  }

  &__content {
    height: 190px;
    overflow: hidden;
    cursor: default;
    @media (min-width: 768px) {
      height: 130px;
    }
  }

  &__title {
    font: var(--font-head-32);
    color: var(--color-white);
    margin-bottom: 17px;
  }

  &__text {
    font: var(--font-body-20);
    color: var(--color-white);
    opacity: 0.6;
  }

  &__btn {
    font: var(--font-button-20);
    color: var(--color-yellow);
    cursor: pointer;
  }

  #infoToggle {
    width: 0;
    height: 0;
    visibility: hidden;
    position: absolute;
  }

  #infoToggle:checked + &__content {
    height: fit-content;
  }
}
