.header {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: var(--color-black);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 20px;
  z-index: 10;

  @media (min-width: 768px) {
    padding: 28px 50px;
  }

  @media (min-width: 1248px) {
    padding: 24px 91px 24px 81px;
  }

  &__logo {
    display: flex;
    svg {
      width: 71.1px;
      height: 24px;
      @media (min-width: 1248px) {
        width: 104px;
        height: 35.11px;
      }
    }
  }
}
