.cart {
  display: flex;
  justify-content: space-between;
  align-items: center;
  row-gap: 16px;
  padding: 4px 0;

  @media (min-width: 768px) {
    padding: 0;
  }

  &:hover {
    .cart__icon {
      .stopLeft {
        stop-color: var(--color-yellow);
      }
      .stopRight {
        stop-color: var(--color-pink);
      }
      opacity: 0.6;
    }
  }

  &__icon {
    width: 24px;
    height: 20px;
  }

  &__count {
    font: var(--font-button-18);
    color: var(--color-white);

    @media (min-width: 768px) {
      display: none;
    }
  }
}
